export default {
  data() {
    return {
      index: -1,
      picker: ['商圈', '社区', '自营', '支局', '商客'],
      index1: -1,
      picker1: ['营业员', '渠道经理', '客户经理'],
      date: '2019-2-25',
      finishdate: '2019-3-28',
      textareaBValue: '',
      modalName: null,
      ruleTitle: '',
      regionId: '',
      regionIdRaw: '',
      ruleType: '',
      role: '',
      ruleDatetimeStart: '',
      ruleDatetimeEnd: '',
      ruleMoney: '',
      ruleNum: '',
      ruleContent: '',
      type: false,
      username: '',
      phone: '',
      card: '',
      job: '',
      password: ''
    };
  },

  components: {
    uniPopup
  },
  methods: {
    check() {
      console.log("=========");
      this.type = true;
    },

    handleClose() {
      this.type = false;
    },

    checkSubmit() {
      this.type = false;
    },

    logPub() {
      let addData = {};
      var seperator2 = ":";
      addData.ruleTitle = this.ruleTitle;
      addData.regionIdRaw = '11';
      addData.ruleType = this.picker[this.index];
      addData.role = this.picker1[this.index1];
      addData.rulePublishDatetime.ruleDatetimeStart = this.date + " " + "00" + seperator2 + "00" + seperator2 + "00";
      addData.rulePublishDatetime.ruleDatetimeEnd = this.finishdate + " " + "00" + seperator2 + "00" + seperator2 + "00";
      addData.ruleMoney = this.ruleMoney;
      addData.ruleNum = this.ruleNum;
      addData.ruleContent = this.ruleContent;
      console.log(addData);
      uni.request({
        url: this.$serverUrl + '/publish/insertPublish',
        data: addData,
        method: 'POST',
        success: res => {
          uni.showToast({
            title: '提交成功',
            icon: 'success',
            duration: 1000
          }); // 							uni.navigateBack({ //提交后返回上一个页面
          // 								delta: 1,
          // 							})
        }
      });
    },

    PickerChange(e) {
      this.index = e.detail.value;
    },

    PickerChange1(e) {
      this.index1 = e.detail.value;
    },

    DateChange(e) {
      this.date = e.detail.value;
    },

    FinishDateChange(e) {
      this.finishdate = e.detail.value;
    }

  }
};